.app {
  max-width: 700px;
  margin: 40px auto;
  padding: 20px;
  font-family: 'Segoe UI', sans-serif;
  text-align: center;
}

textarea {
  width: 100%;
  height: 120px;
  padding: 10px;
  font-size: 16px;
  resize: vertical;
}

button {
  margin-top: 15px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #0077cc;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

button:disabled {
  background-color: #999;
  cursor: not-allowed;
}

.response {
  margin-top: 30px;
  text-align: left;
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
}

h3 {
  margin-bottom: 8px;
}

pre {
  background: #eef;
  padding: 10px;
  border-radius: 5px;
  overflow-x: auto;
}